import React, { Component } from "react";
import { connect } from "react-redux";
import { finishSend, progressSend } from "../actions/LogicAction";
import { gagal } from "../Alert";
import { AxiosMasterGetWithParams } from "../Axios";
import { checkLaporan } from "../helper";
import FormValidasi from "./Component/FormValidasi";
import LaporanValidasi from "./Component/LaporanValidasi";

class laporanValidasi extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  handleProses(data) {
    this.props.dispatch(progressSend());
    let params = {
      no_bon: data.no_bon || "-",
      user_id: data.user_id || "-",
      jenis_transaksi: data.jenis_transaksi || "-",
      start_date: data.mulai_dari || "-",
      end_date: data.sampai_dengan || "-",
    };
    AxiosMasterGetWithParams(`report/validasi-transaksi/`, params)
      .then((res) => {
        this.props.dispatch(finishSend());
        if (checkLaporan(res.data)) {
          LaporanValidasi(res.data);
        }
      })
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
          this.props.dispatch(finishSend())
        )
      );
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="row">
                <div className="col-lg-12 col-md-12">
                  <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                    <li className="nav-item">
                      <a
                        href="#proses-tab"
                        data-toggle="tab"
                        aria-expanded="false"
                        className="nav-link active"
                      >
                        <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                        <span className="d-none d-lg-block">
                          Laporan Validasi
                        </span>
                      </a>
                    </li>
                  </ul>
                  <div className="tab-content">
                    <div className="tab-pane show active" id="proses-tab">
                      <FormValidasi
                        from="PROSES"
                        onSubmit={(data) => this.handleProses(data)}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect()(laporanValidasi);
